import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  CourseGroupAddEditVM,
  CourseGroupGetVM,
} from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-course-courseGroup",
  templateUrl: "./course-Group.component.html",
  styleUrls: ["./course-Group.component.scss"],
})
export class CourseGroupComponent implements OnInit {
  CourseGroups: CourseGroupGetVM[] = [];
  myForm: FormGroup;
  submitted: boolean = false;
  // curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = [
    "#",
    "Ar_Name",
    "En_Name",
    "Min_Sub_Num",
    "Max_Sub_Num",
    "actions",
  ];
  dataSource: MatTableDataSource<CourseGroupGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private courseServ: CourseService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      Ar_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      En_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      Max_Sub_Num: ["", [Validators.required, CustomeValidator.minusNum]],
      Min_Sub_Num: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    this.getAllCourseGroups(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllCourseGroups(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all course Groups
  getAllCourseGroups(page: number) {
    // this.curPage = page;

    this.spinner.show();
    this.courseServ.getAllCourseGroups(page).subscribe((res) => {
      if (res) {
        this.CourseGroups = res.courseGroupList;
        this.dataSource = new MatTableDataSource(this.CourseGroups);
        this.itemsCounts = res.countItems;
        this.dataSource.sort = this.sort;
      }
      this.spinner.hide();
    });
  }

  // get form values to update
  toEditCourseGroup(courseGroup: CourseGroupGetVM) {
    this.myForm.patchValue(courseGroup);
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      if (this.formCtrls.Min_Sub_Num?.value > this.formCtrls.Max_Sub_Num?.value)
        return this.ToastrMsg.error("EducationalSetup.maxSubGreaterThanMin");

      let courseGroup: CourseGroupAddEditVM = {
        id: this.formCtrls.id?.value,
        EducationalComponentID: this.EduCompId,
        Ar_Name: this.formCtrls.Ar_Name?.value,
        En_Name: this.formCtrls.En_Name?.value,
        Max_Sub_Num: this.formCtrls.Max_Sub_Num?.value,
        Min_Sub_Num: this.formCtrls.Min_Sub_Num?.value,
      };
      console.log("CourseGroup", courseGroup);

      this.courseServ.addEditCourseGroup(courseGroup).subscribe((res: any) => {
        if (res.returnValue == 1) {
          // let returnObject = res.course_group;
          // let returnData: CourseGroupGetVM = {
          //   id: returnObject.id,
          //   Ar_Name: returnObject.Ar_Name,
          //   En_Name: returnObject.En_Name,
          //   Max_Sub_Num: returnObject.Max_Sub_Num,
          //   Min_Sub_Num: returnObject.Min_Sub_Num,
          // };

          courseGroup.id == 0
            ? this.addCourseGroup(res.course_group)
            : this.updateCourseGroup(res.course_group);
          this.dataSource.sort = this.sort;
          this.clearForm();
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add course Group
  addCourseGroup(res: CourseGroupGetVM) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.ToastrMsg.success("General.AddSuccessfully");
  }

  // update course Group
  updateCourseGroup(res: CourseGroupGetVM) {
    let i = this.dataSource.data.findIndex((e) => e.id == res.id);
    this.dataSource.data[i] = res;
    this.ToastrMsg.success("General.UpdateSuccessfully");
  }

  // delete dialog
  openDeleteDialog(courseGroup: CourseGroupGetVM): void {
    let courseGroupName =
      this.currentLang == "ar" ? courseGroup.Ar_Name : courseGroup.En_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseGroupName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCourseGroup(courseGroup);
      });
  }

  // delete course Group
  deleteCourseGroup(courseGroup: CourseGroupGetVM) {
    this.spinner.show();
    this.courseServ.deleteCourseGroup(courseGroup.id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == courseGroup);
        this.dataSource.data.splice(i, 1);
        // this.itemsCounts--;
        this.dataSource.sort = this.sort;

        if (courseGroup.id == this.formCtrls.id.value) this.clearForm();

        this.ToastrMsg.success("General.DeleteSuccessfully");
      } else this.ToastrMsg.error(res?.returnString);
      this.spinner.hide();
    });
  }

  clearForm() {
    this.myForm.reset();
    this.formCtrls.id.setValue(0);
    this.submitted = false;
  }
}
