import { MessagesService } from "./../../../../../../shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { TranslateService } from "@ngx-translate/core";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ActivatedRoute } from "@angular/router";
import {
  ExamNightModel,
  GetExamNightModel,
} from "app/dashboard/models/setup/educational-setup/exam-night";
import { ExamNightService } from "app/dashboard/services/setup/educational-setup/exam-night.service";
import { IfileVM, IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MatDialog } from "@angular/material/dialog";

@Component({
  selector: "app-exam-night",
  templateUrl: "./exam-night.component.html",
  styleUrls: ["./exam-night.component.scss"],
})
export class ExamNightComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  functionId: number;
  EduCompId: number;
  rowFunctions: IRowFunctionVM[];
  Filepath = null;
  fileType: string;
  currentLang: string;
  itemsCounts: number = 0;

  displayedColumns: string[] = ["#", "name", "subject", "download", "actions"];
  dataSource: MatTableDataSource<ExamNightModel>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private uploadFileService: UploadFileService,
    private examNightService: ExamNightService,
    public authService: AuthService,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private toastr: MessagesService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllExamNight(1);
    this.myForm = this.fb.group({
      id: [0],
      countryId: [""],
      stageId: [""],
      educationYearId: [""],
      subjectId: [null, Validators.required],
      file: [null, Validators.required],
      name: [null, Validators.required],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      //   this.getAllExamNight(1);
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
      this.dataSource.sort = this.sort;
    }
  }

  getAllExamNight(page: number) {
    this.spinner.show();
    this.examNightService
      .getAllExamNight(page)
      .subscribe((res: GetExamNightModel) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.list);
          this.itemsCounts = res.itemsCounts;
        }
        this.spinner.hide();
      });
  }

  addExamNight() {
    this.submitted = true;
    let body: ExamNightModel = {
      id: this.FormCtrls.id.value,
      subjectId: this.FormCtrls.subjectId.value,
      name: this.FormCtrls.name.value,
      file: this.FormCtrls.file.value,
    };
    if (this.myForm.valid) {
      this.examNightService.addExamNight(body).subscribe((response) => {
        if (response) {
          this.clearForm();
          this.getAllExamNight(1);
          this.toastr.success("General.AddSuccessfully");
          console.log("EXAM NIGHT: ", response);
        }
      });
    } else this.toastr.error("General.FormNotValid");
  }

  openDeleateDialog(file: ExamNightModel): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: file.name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteFile(file);
      });
  }

  deleteFile(file: ExamNightModel) {
    this.examNightService.deleteFile(file.id).subscribe((response) => {
      if (response) {
        let i = this.dataSource.data.findIndex((e) => e == file);
        this.dataSource.data.splice(i, 1);
        this.itemsCounts--;
        this.dataSource.sort = this.sort;
        this.toastr.success("General.DeleteSuccessfully");
      } else {
        this.toastr.error("General.TosterErrorMessage");
      }
    });
  }

  //Attache File
  onChange($event) {
    this.uploadFileService.uploadFile($event).then((file: IfileVM) => {
      console.log("file", file);
      this.FormCtrls.file?.setValue(file);
      // this.fileType = file.type;
      // this.Filepath = file.FileAsBase64?.replace(
      //   "data:application/pdf;base64,",
      //   ""
      // );
    });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
    const FileInputs = document.querySelectorAll('input[type="file"]');
    FileInputs.forEach((input: any) => {
      input.value = null;
    });
  }
}
