import { TranslateService } from "@ngx-translate/core";
import { MessagesService } from "app/shared/services/messages.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { AddLessonComponent } from "./../lessons/add-lesson/add-lesson.component";
import { AddUnitComponent } from "./../add-unit/add-unit.component";
import { Component, Input, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute, Router } from "@angular/router";
import {
  IRowFunctionVM,
  IUserVM,
  UserModel,
} from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";
import {
  ILessonVM,
  IUnitVM,
} from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { GeneralService } from "app/shared/services/General/general.service";

@Component({
  selector: "app-strcture-index",
  templateUrl: "./strcture-index.component.html",
  styleUrls: ["./strcture-index.component.scss"],
})
export class StrctureIndexComponent implements OnInit {
  EduCompId: number;
  branchId: number;
  subjectId: number;
  functionId: number;
  userId: number;
  selectedUnitId: number;
  selectedUnitName: string;
  rowFunctions: IRowFunctionVM[];
  units: IUnitVM[] = [];
  AddedLesson: ILessonVM;
  @Input() canUpdate: boolean = true; // if (true) current user is (admin), if (false) current user is (parent or student)
  course: CourseGetVM;
  currentLang: string;
  userData: UserModel;
  isParent: boolean = false; // current user is parent
  isStudent: boolean = false; //current user is student

  constructor(
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private courceStructureService: CourceStructureService,
    private courseServ: CourseService,
    private router: Router,
    private fb: FormBuilder,
    public authserv: AuthService,
    private msg: MessagesService,
    private dialog: MatDialog,
    private generalService: GeneralService,
    private translate: TranslateService
  ) {
    route.paramMap.subscribe((params) => {
      this.subjectId = Number(params.get("subjectId"));
      this.userId = Number(params.get("userId")); // get userId if parent or student

      if (router.url.search("/parent/") >= 0) this.isParent = true; // current user is parent
      if (router.url.search("/student/") >= 0) this.isStudent = true; // current user is student
      console.log("isStudent", this.isStudent);
      console.log("isParent", this.isParent);
      if (this.userId) {
        generalService.getUser(this.userId).subscribe((res: IUserVM) => {
          this.userData = res.user;
        });
      }
    });
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    courseServ.getCourseById(this.subjectId).subscribe((res) => {
      this.course = res[0];
    });
  }

  ngOnInit(): void {
    console.log("canUpdate", this.canUpdate);
    this.getUnits();
  }

  //Get Subject Structre
  getUnits() {
    this.spinner.show();
    this.courceStructureService
      .getUnits(this.subjectId)
      .subscribe((res: IUnitVM[]) => {
        this.units = res;
        this.selectedUnitId = res[0].id;
        this.selectedUnitName = res[0].unit_ar_name;
        this.spinner.hide();
      });
  }

  SelectUnitId(unit: IUnitVM) {
    this.selectedUnitId = unit.id;
    this.selectedUnitName = unit.unit_ar_name;
  }

  deleteUnit(unit: IUnitVM) {
    this.spinner.show();
    this.courceStructureService.deleteUnit(unit.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let i = this.units.indexOf(unit);
        this.units.splice(i, 1);
        this.msg.success("General.DeleteSuccessfully");
      }
      this.spinner.hide();
    });
  }

  openDeleateDialog(unit: IUnitVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: unit.unit_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteUnit(unit);
      });
  }

  openAddUnitDialog(unitId): void {
    this.dialog
      .open(AddUnitComponent, {
        data: {
          unitId: unitId > 0 ? unitId : 0,
          subjectId: this.subjectId,
          canUpdate: this.canUpdate,
        },
        minWidth: "60vw",
        // minHeight: "60vh",
      })
      .afterClosed()
      .subscribe((unit: IUnitVM) => {
        if (unit?.id > 0) {
          let i = this.units.findIndex((e) => e.id == unit.id);
          i < 0 ? this.units.push(unit) : (this.units[i] = unit);
        }
      });
  }

  openAddLessonDialog(): void {
    this.dialog
      .open(AddLessonComponent, {
        data: {
          unitId: this.selectedUnitId,
          lesson: {
            id: 0,
          },
        },
        minWidth: "60vw",
        //minHeight: "60vh",
      })
      .afterClosed()
      .subscribe((lesson: ILessonVM) => {
        if (lesson?.id > 0) this.AddedLesson = lesson;
      });
  }

  back() {
    if (this.isParent)
      this.router.navigateByUrl(`/parent/child-evaluation/${this.userId}`);
    if (this.isStudent) this.router.navigateByUrl("/student/mySpace");
  }

  track(index: number, el: any): number {
    return el.id;
  }
}
