import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  CourseGetVM,
  IUploadSubjectSyllabusModel,
} from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IfileVM, IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";

@Component({
  selector: "app-course-index",
  templateUrl: "./course-index.component.html",
  styleUrls: ["./course-index.component.scss"],
})
export class CourseIndexComponent implements OnInit {
  Courses: CourseGetVM[] = [];
  // curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Edit" },
  //   { functionName: "Delete" },
  //   { functionName: "Lecture" },
  //   { functionName: "courseStructure" },
  //   { functionName: "UploadSyllabus" },
  // ];

  // table
  displayedColumns: string[] = [
    "#",
    "subject_name",
    "eduYear",
    "MinScore",
    "MaxScore",
    "price",
    "subjectCode",
    "subjectDescription",
    "ImagePath",
    "PromoPath",
    "actions",
  ];
  dataSource: MatTableDataSource<CourseGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private CourseServ: CourseService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService,
    public uploadFileService: UploadFileService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllCourses(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllCourses(1);
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "eduYear":
          return this.currentLang == "ar"
            ? item.eduYear_ar_name ?? item.eduYear_en_name
            : item.eduYear_en_name ?? item.eduYear_ar_name;
        case "subject_name":
          return this.currentLang == "ar"
            ? item.subject_ar_name ?? item.subject_en_name
            : item.subject_en_name ?? item.subject_ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  // get all courses
  getAllCourses(page: number) {
    // this.curPage = page;
    this.spinner.show();

    this.CourseServ.getAllCoursesByEduCopmIdPage(
      this.EduCompId,
      page
    ).subscribe((res) => {
      if (res) {
        this.Courses = res.subjectList;
        this.dataSource = new MatTableDataSource(this.Courses);
        this.itemsCounts = res.countItems;
        this.startSortingTable();
      }
      this.spinner.hide();
    });
  }

  // delete dialog
  openDeleteDialog(course: CourseGetVM): void {
    let courseName =
      this.currentLang == "ar"
        ? course.subject_ar_name
        : course.subject_en_name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: courseName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCourse(course);
      });
  }

  // delete course
  deleteCourse(course: CourseGetVM) {
    this.spinner.show();
    this.CourseServ.deleteCourse(course.id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        let i = this.dataSource.data.findIndex((e) => e == course);
        this.dataSource.data.splice(i, 1);
        // this.itemsCounts--;
        this.dataSource.sort = this.sort;

        this.ToastrMsg.success("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }

  onUploadSyllabus(event: any, subjectId: number) {
    this.uploadFileService.uploadFile(event).then((file: IfileVM) => {
      setTimeout(() => {
        if (file.FileAsBase64) this.uploadSubjectSyllabus(file, subjectId);
      }, 1000);
    });
  }

  uploadSubjectSyllabus(file: IfileVM, subjectId: number) {
    let body = {} as IUploadSubjectSyllabusModel;
    body.subjectId = subjectId;
    body.thumbnailFile = file;
    if (file) {
      this.spinner.show();
      this.CourseServ.uploadSubjectSyllabus(body).subscribe((res: any) => {
        if (res.returnValue === 1) {
          this.ToastrMsg.success("General.UploadSuccessfully");
        }
        this.spinner.hide();
      });
    } else this.ToastrMsg.warning("labels.MustSelectFile");
  }
}
