import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class GroupService {
  constructor(private http: HttpClient) {}

  //#region >> groups
  // Return All  Groups by page
  getAllGroups<T>(page: number) {
    return this.http.get<T>(`${environment.baseUrl}Groups/getAllGroups/${page}`);
  }

  // Return All Groups > dropDown list
  getAllGroupsDropDown<T>() {
    return this.http.get<T[]>(
      `${environment.baseUrl}Groups/getAllGroupsDropDown`
    );
  }

  // Add New Group
  addGroup<T>(data: T) {
    return this.http.post(`${environment.baseUrl}Groups/addGroup`, data);
  }

  // Update Group
  updateGroup<T>(id: number, data: T) {
    return this.http.put(`${environment.baseUrl}Groups/updateGroup${id}`, data);
  }

  // Delete Group
  deleteGroup(id: number) {
    return this.http.delete(`${environment.baseUrl}Groups/deleteGroup/${id}`);
  }
  //#endregion

  //#region >> subgroups
  // Return All Subgroup by page
  getAllSubgroups<T>(page: number) {
    return this.http.get<T>(
      `${environment.baseUrl}Subgroups/getAllSubgroups/${page}`
    );
  }

  // Add New Subgroup
  addSubgroup<T>(data: T) {
    return this.http.post(`${environment.baseUrl}Subgroups/addSubgroup`, data);
  }

  // Update Subgroup
  updateSubgroup<T>(id: number, data: T) {
    return this.http.put(
      `${environment.baseUrl}Subgroups/updateSubgroup${id}`,
      data
    );
  }

  // Delete Subgroup
  deleteSubgroup(id: number) {
    return this.http.delete(
      `${environment.baseUrl}Subgroups/deleteSubgroup/${id}`
    );
  }
  //#endregion
}
