import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { DepartmentService } from "app/dashboard/services/setup/educational-setup/department.service";
import {
  IAddEditDepartment,
  IDepartment,
} from "app/dashboard/models/setup/educational-setup/department";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { IEductionalGroupVM } from "app/dashboard/models/setup/general-setup/educational-group";
import { EducationalGroupService } from "app/dashboard/services/setup/general-setup/educational-group.service";

@Component({
  selector: "app-department",
  templateUrl: "./department.component.html",
  styleUrls: ["./department.component.scss"],
})
export class DepartmentComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curLang: string;
  EduCompId: number;
  EduGroups: IEductionalGroupVM[] = [];
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Update" },
  //   { functionName: "Delete" },
  // ];
  displayColumns: string[] = [
    "#",
    "ar_name",
    "en_name",
    // "stage",
    // "EducationalYear",
    "educationGroup",
    "option",
  ];
  dataSource: MatTableDataSource<IDepartment>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    public authService: AuthService,
    private departmentService: DepartmentService,
    private EducationalGroupServ: EducationalGroupService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      eduGroupId: ["", [Validators.required]],
      // stageId: ["", Validators.required],
      // eduYearId: ["", Validators.required],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.returnAllDepartments(1);
    this.getAllEducationalGroups();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnAllDepartments(1);
      this.getAllEducationalGroups();
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "educationGroup":
          return this.curLang == "ar"
            ? item.eduGroupArName
            : item.eduGroupEnName;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  editDepartment(DepartmentData: IDepartment) {
    this.myForm.patchValue(DepartmentData);
    this.FormCtrls.id.setValue(DepartmentData.Dep_id);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // get all educational groups
  getAllEducationalGroups() {
    this.spinner.show();
    this.EducationalGroupServ.getEducationalGroupDropdown().subscribe(
      (res: IEductionalGroupVM[]) => {
        if (res) this.EduGroups = res;
        this.spinner.hide();
      }
    );
  }

  // Return All Departments
  returnAllDepartments(page: number) {
    this.spinner.show();
    this.departmentService.getDepartment(page).subscribe((res) => {
      if (res) {
        this.dataSource = new MatTableDataSource(res.DepList);
        this.itemsCounts = res.countItems;
        this.startSortingTable();
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    let departmentData: IAddEditDepartment = {
      id: this.FormCtrls.id.value,
      // stageId: this.FormCtrls.stageId.value,
      // eduYearId: this.FormCtrls.eduYearId.value,
      ar_name: this.FormCtrls.ar_name.value,
      en_name: this.FormCtrls.en_name.value,
      eduGroupId: this.FormCtrls.eduGroupId.value,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.departmentService
        .addEditDepartment(departmentData)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            let returnObject = res.Department;
            let returnData: IDepartment = {
              Dep_id: returnObject.id,
              ar_name: returnObject.ar_name,
              en_name: returnObject.en_name,
              eduGroupId: returnObject.eduGroupId,
              eduGroupArName: returnObject.eduGroup_ar_name,
              eduGroupEnName: returnObject.eduGroup_en_name,
            };

            departmentData.id == 0
              ? this.addDepartment(returnData)
              : this.updateDepartment(returnData);
            this.dataSource.sort = this.sort;
            this.clearForm();
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add Department
  addDepartment(res: IDepartment) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");
  }

  // Update Department
  updateDepartment(res: IDepartment) {
    let index = this.dataSource.data.findIndex((e) => e.Dep_id == res.Dep_id);
    this.dataSource.data[index] = res;
    this.toastr.success("General.UpdateSuccessfully");
  }

  openDeleateDialog(DepartmentData: IDepartment): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg:
            this.curLang == "ar"
              ? DepartmentData.ar_name
              : DepartmentData.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteDepartment(DepartmentData);
      });
  }

  // Start:Delete Department
  deleteDepartment(DepartmentData: IDepartment) {
    this.departmentService
      .deleteDepartment(DepartmentData.Dep_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex(
            (e) => e == DepartmentData
          );
          this.dataSource.data.splice(index, 1);
          // this.itemsCounts--;
          this.dataSource.sort = this.sort;

          if (DepartmentData.Dep_id == this.FormCtrls.id.value)
            this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
