import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import {
  IAddEditAssignClassRoomVM,
  IAssignClassRoom,
  IAssignClassRoomList,
} from "app/dashboard/models/setup/educational-setup/assign-class-rooms";
import { ClassRoomGetVM } from "app/dashboard/models/setup/general-setup/classes/class-room";
// import { IEducationYear } from "app/dashboard/models/setup/educational-setup/education-year";
import { AsignClassRoomsService } from "app/dashboard/services/setup/educational-setup/asign-class-rooms.service";
import { ClassRoomService } from "app/dashboard/services/setup/general-setup/classes/class-room.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-class-rooms",
  templateUrl: "./asign-class-rooms.component.html",
  styleUrls: ["./asign-class-rooms.component.scss"],
})
export class AsignClassRoomsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  academicEduYearId: number;
  curLang: string;
  EduCompId: number;
  classRoomList: ClassRoomGetVM[];
  // educationalYearList: IEducationYear[];
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any = [{ functionName: "Update" }, { functionName: "Delete" }];
  displayColumns: string[] = [
    "#",
    "ClassRoom",
    // "EducationalYear",
    "option",
  ];
  dataSource: MatTableDataSource<IAssignClassRoom>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    public authService: AuthService,
    private asignClassRoomsService: AsignClassRoomsService,
    private educationYearService: EducationYearService,
    private classRoomService: ClassRoomService
  ) {
    this.route.paramMap.subscribe(
      (pram) => (this.academicEduYearId = Number(pram.get("yearId")))
    );

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      // academicEduYearId: ["", Validators.required],
      classRoomId: ["", [Validators.required]],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.getAsignClassRoom(1);
    // this.getAllEducationalYear();
    this.getAllClassRooms();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAsignClassRoom(1);
      // this.getAllEducationalYear();
      this.getAllClassRooms();
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "ClassRoom":
          return item.classRoomCode;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  editAsignClassRoom(AsignClassRoomData: IAssignClassRoom) {
    this.myForm.patchValue(AsignClassRoomData);
    this.FormCtrls.id.setValue(AsignClassRoomData.academicClassRoomId);
  }

  // Return ClassRooms
  getAllClassRooms() {
    this.classRoomService
      .getAllClassRoomsDropDown(this.EduCompId)
      .subscribe((res) => {
        if (res) this.classRoomList = res;
      });
  }

  // // Return Educational Years
  // getAllEducationalYear() {
  //   this.educationYearService.getEducationalYearDropdown().subscribe((res) => {
  //     this.educationalYearList = res.eduYearList;
  //   });
  // }

  // Return Asign ClassRoom
  getAsignClassRoom(page: number) {
    this.spinner.show();
    this.asignClassRoomsService
      .getAsignClassRoomsByYearId(this.academicEduYearId, page)
      .subscribe((res: IAssignClassRoomList) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.academicClassRoom);
          this.itemsCounts = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    let AsignClassRoomData: IAddEditAssignClassRoomVM = {
      id: this.FormCtrls.id.value,
      academicEduYearId: this.academicEduYearId,
      classRoomId: this.FormCtrls.classRoomId.value,
      eduCompId: this.EduCompId,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.asignClassRoomsService
        .addEditAsignClassRoom(AsignClassRoomData)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            // let returnObject = res.academic_ClassRoom;
            // let returnData: IAssignClassRoom = {
            //   academicClassRoomId: returnObject.id,
            //   academicEduYearId: returnObject.academicEduYearId,
            //   classRoomId: returnObject.classRoomId,
            //   classRoomCode: returnObject.classRoomCode,
            // };
            res.academic_ClassRoom.academicClassRoomId =
              res.academic_ClassRoom.id;

            AsignClassRoomData.id == 0
              ? this.addAsignClassRoom(res.academic_ClassRoom)
              : this.updateAsignClassRoom(res.academic_ClassRoom);
            this.dataSource.sort = this.sort;
            this.clearForm();
            this.submitted = false;
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  // add Asign ClassRoom
  addAsignClassRoom(res: IAssignClassRoom) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");
  }

  // Update Asign ClassRoom
  updateAsignClassRoom(res: IAssignClassRoom) {
    let i = this.dataSource.data.findIndex(
      (e) => e.academicClassRoomId == res.academicClassRoomId
    );
    this.dataSource.data[i] = res;
    this.toastr.success("General.UpdateSuccessfully");
  }

  openDeleateDialog(AsignClassRoomData: IAssignClassRoom): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: AsignClassRoomData.academicClassRoomId,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteAsignClassRoom(AsignClassRoomData);
      });
  }

  // Delete  Asign ClassRoom
  deleteAsignClassRoom(AsignClassRoomData: IAssignClassRoom) {
    this.asignClassRoomsService
      .deleteAsignClassRoom(AsignClassRoomData.academicClassRoomId)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex(
            (e) => e == AsignClassRoomData
          );
          this.dataSource.data.splice(index, 1);
          // this.itemsCounts--;
          this.dataSource.sort = this.sort;

          if (AsignClassRoomData.academicClassRoomId == this.FormCtrls.id.value)
            this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
