import { Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute, Router } from "@angular/router";
import { IfileVM, IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { StudentGroupService } from "app/dashboard/services/setup/educational-setup/student-group.service";
import {
  StudentSearchVM,
  StudentGetVM,
} from "app/dashboard/models/setup/educational-setup/student-group";
import { MessagesService } from "app/shared/services/messages.service";

@Component({
  selector: "app-add-student-to-group",
  templateUrl: "./add-student-to-group.component.html",
  styleUrls: ["./add-student-to-group.component.scss"],
})
export class AddStudentToGroupComponent implements OnInit {
  students: StudentGetVM[] = [];
  groupId: number = 0;

  itemsCounts: number = 0;
  curPage: number = 1;
  myForm: FormGroup;

  ExcelFile: IfileVM = null;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Delete" }];

  displayedColumns: string[] = ["#", "Name", "Email", "Mobile", "actions"];
  dataSource: MatTableDataSource<StudentGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private route: ActivatedRoute,
    private authserv: AuthService,
    private router: Router,
    private fb: FormBuilder,
    private studentGroupServ: StudentGroupService,
    private toastrMsg: MessagesService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    this.route.queryParamMap.subscribe((params) => {
      this.groupId = Number(params.get("groupId"));

      this.functionId = Number(params.get("functionId"));
      //  console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res) {
              this.rowFunctions = res;
              //  console.log(`RowFunctions for"${this.functionId}": `, res);
            }
          });
      }
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit() {
    this.myForm = this.fb.group({
      Email: [""],
      Mobile: [""],
    });

    this.getAllStudentsInGroup(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllStudentsInGroup(1);
    });

    // this.authserv.branchId.subscribe((e) => {
    //   this.getAllStudentsInGroup(1);
    // });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  getAllStudentsInGroup(page: number) {
    this.curPage = page;

    let searchData: StudentSearchVM = {
      Mobile: this.formCtrls?.Mobile?.value?.trim(),
      Email: this.formCtrls?.Email?.value?.trim(),
      Page: page,
      GroupId: this.groupId,
      EduCompId: this.EduCompId,
    };

    this.spinner.show();
    this.studentGroupServ
      .getAllStudentsInGroup(searchData)
      .subscribe((res: any) => {
        if (res) {
          this.students = res.StudentList;
          this.dataSource = new MatTableDataSource(this.students);
          this.itemsCounts = res.countItems;
          // console.log("students", this.students);
        }
        this.spinner.hide();
      });
  }

  addStudent() {
    let searchData: StudentSearchVM = {
      Mobile: this.formCtrls?.Mobile?.value,
      Email: this.formCtrls?.Email?.value,
      Page: 1,
      GroupId: this.groupId,
      EduCompId: this.EduCompId,
    };

    this.spinner.show();
    console.log("search data", searchData);
    this.studentGroupServ.checkStudent(searchData).subscribe((student) => {
      // console.log("check student", student);
      if (student.InGroup) {
        this.toastrMsg.info("EducationalSetup.studentExistInGroup");
        return;
      } else {
        this.spinner.show();
        // console.log("student", student);
        this.studentGroupServ
          .addStudent(student.student?.userId, this.groupId)
          .subscribe((res: any) => {
            // console.log('addStudent', res);
            // if (
            //   res?.returnString ==
            //   "Student already exists in a group with the same teacher and subject"
            // )
            //   this.toastrMsg.info("EducationalSetup.studentExistInGroup");
            if (res?.returnValue == 1) {
              this.getAllStudentsInGroup(this.curPage);
              this.toastrMsg.success("General.AddSuccessfully");
            } else this.toastrMsg.error(res?.returnString);
            this.spinner.hide();
          });
      }
    });
  }

  openDeleteDialog(student: StudentGetVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: student.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteStudent(student);
      });
  }

  deleteStudent(student: StudentGetVM) {
    this.spinner.show();
    this.studentGroupServ
      .deleteStudent(student.userId, this.groupId)
      .subscribe((res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => (e = student));
          this.dataSource.data.splice(i, 1);

          // this.dataSource.paginator = this.paginator;
          this.toastrMsg.success("General.DeleteSuccessfully");
        } else this.toastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
  }

  uploadStudentsByExcel() {
    if (this.ExcelFile == null)
      this.toastrMsg.warning("UploadStudentGrades.fileErr");
    else {
      this.spinner.show();
      this.studentGroupServ
        .uploadStudentsByExcel({
          ExcelFile: this.ExcelFile,
          groupId: this.groupId,
        })
        .subscribe((res: any) => {
          if (res.returnValue == 1)
            this.toastrMsg.success("General.UploadSuccessfully");
          else this.toastrMsg.error(res?.returnString);
          this.spinner.hide();
        });
    }
  }

  onChange(event: any) {
    let fileName = event.target.files[0].name;
    let fileSize = event.target.files[0].size;
    let fileType = event.target.files[0].type;
    let LastModified = event.target.files[0].lastModified;
    let LastModifiedDate = event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;

        let data: IfileVM = {
          FileAsBase64: fileReder.replace(
            "data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,",
            ""
          ),
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.ExcelFile = data;
      };
    }
  }

  clearSearch() {
    this.myForm.reset();
    this.getAllStudentsInGroup(1);
  }
}
