import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  IAddEditDepartment,
  IDepartment,
  IDepartmentList,
} from "app/dashboard/models/setup/educational-setup/department";

@Injectable({
  providedIn: "root",
})
export class DepartmentService {
  constructor(private http: HttpClient) {}

  // Return All Departments
  getDepartment(page: number) {
    return this.http.get<IDepartmentList>(
      `${environment.baseUrl}Department/getDepartmentByPage/${page}`
    );
  }

  getDepartmentDropdown() {
    return this.http.get<IDepartment[]>(
      `${environment.baseUrl}Department/getAllDepartment`
    );
  }

  //Add - edit New Department
  addEditDepartment(data: IAddEditDepartment) {
    return this.http.post(
      `${environment.baseUrl}Department/addEditDepartment`,
      data
    );
  }

  //Delete Department
  deleteDepartment(DepartmentId: number) {
    return this.http.delete(
      `${environment.baseUrl}Department/deleteDepartment/${DepartmentId}`
    );
  }
}
