import { MessagesService } from "app/shared/services/messages.service";
import { MatDialog } from "@angular/material/dialog";
import { Component, OnInit, Input } from "@angular/core";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { NgxSpinnerService } from "ngx-spinner";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";
import { ILessonVM } from "app/dashboard/models/setup/educational-setup/cource-structure";
import { AddLessonComponent } from "../add-lesson/add-lesson.component";
import { TopicsComponent } from "../../topics/topics.component";

@Component({
  selector: "app-lessons-index",
  templateUrl: "./lessons-index.component.html",
  styleUrls: ["./lessons-index.component.scss"],
})
export class LessonsIndexComponent implements OnInit {
  @Input("unitId") unitId: number;
  @Input("AddedLesson") AddedLesson: ILessonVM;
  @Input() canUpdate: boolean = true;
  lessons: ILessonVM[];
  constructor(
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    private dialog: MatDialog,
    private courceStructureService: CourceStructureService
  ) {}

  ngOnInit(): void {}
  ngOnChanges() {
    this.unitId > 0 ? this.getLessons() : null;
    //  console.log("Added Lesson=>: ", this.AddedLesson);
    if (this.AddedLesson) this.lessons.push(this.AddedLesson);
  }

  getLessons() {
    this.spinner.show();
    this.courceStructureService
      .getLessons(this.unitId)
      .subscribe((res: ILessonVM[]) => {
        this.lessons = res;
        this.spinner.hide();
      });
  }

  deleteLesson(lesson: ILessonVM) {
    this.spinner.show();
    this.courceStructureService
      .deleteLesson(lesson.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.lessons.indexOf(lesson);
          this.lessons.splice(i, 1);
          this.msg.success("General.DeleteSuccessfully");
        }
        this.spinner.hide();
      });
  }

  openDeleateDialog(lesson: ILessonVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: lesson.lesson_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteLesson(lesson);
      });
  }

  openAddLessonDialog(lesson: ILessonVM): void {
    this.dialog
      .open(AddLessonComponent, {
        data: {
          unitId: this.unitId,
          lesson: lesson,
        },
        minWidth: "60vw",
        //  minHeight: "60vh",
      })
      .afterClosed()
      .subscribe((updatedLesson: ILessonVM) => {
        //console.log("updated Lesson=>: ", updatedLesson);
        let i = this.lessons.findIndex((e) => e.id == updatedLesson.id);
        this.lessons[i] = updatedLesson;
      });
  }

  openAddTopicDialog(lessonId: number): void {
    this.dialog
      .open(TopicsComponent, {
        data: {
          lessonId: lessonId,
          lesson: null,
        },

        minWidth: "85vw",
        maxHeight: "95vh",
      })
      .afterClosed()
      .subscribe((topic) => {});
  }
}
