import { WeeklyTableService } from "app/dashboard/services/educational-tabels/weekly-table.service";
import { IWeeklyLectureVM } from "app/dashboard/models/educational-tabels/weekly-table";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { ICourseLectureAddEditVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseLectureService } from "app/dashboard/services/setup/educational-setup/course-lecture.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-course-lecture",
  templateUrl: "./course-lecture.component.html",
  styleUrls: ["./course-lecture.component.scss"],
})
export class CourseLectureComponent implements OnInit {
  courseId: number = 0;
  lecturesList: IWeeklyLectureVM[];
  IsAdd: boolean = true;

  myForm: FormGroup;
  submitted: boolean = false;
  currentLang: string;

  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private CourseLectureServ: CourseLectureService,
    private WeeklyTableServ: WeeklyTableService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    // get course id
    route.paramMap.subscribe((params) => {
      let id = Number(params.get("courseId"));
      if (id) {
        this.courseId = id;
        this.CourseLectureServ.getAllLecturesIdsByCourseId(
          this.courseId
        ).subscribe((res) => {
          if (res) {
            if (res.length > 0) this.IsAdd = false;
            this.WeeklyLecIDSCtrls.setValue(res.map((lec) => lec.Lecture_id));
          }
        });
      }
    });

    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      WeeklyLec_IDS: ["", [Validators.required]],
    });

    this.getAllWeeklyLectures();
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllWeeklyLectures();
    });
  }

  get WeeklyLecIDSCtrls() {
    return this.myForm.controls.WeeklyLec_IDS;
  }

  getAllWeeklyLectures() {
    this.spinner.show();
    this.WeeklyTableServ.getAllWeeklyLectures().subscribe((res:IWeeklyLectureVM[]) => {
      if (res) this.lecturesList = res;
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let courseLectures: ICourseLectureAddEditVM = {
        subject_id: this.courseId,
        WeeklyLec_IDS: this.WeeklyLecIDSCtrls.value,
      };
      // console.log("WeeklyLecIDS", courseLectures);

      this.spinner.show();
      this.IsAdd
        ? this.addcourseLectures(courseLectures)
        : this.updatecourseLectures(courseLectures, false);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add course Group
  addcourseLectures(courseLectures: ICourseLectureAddEditVM) {
    this.CourseLectureServ.addCourseLectures(courseLectures).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          this.IsAdd = false;
          // get lectures ids from response after add
          this.WeeklyLecIDSCtrls.setValue(
            res.Course_Lecture.map((lec) => lec.Lecture_id)
          );
          this.ToastrMsg.success("General.AddSuccessfully");
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      }
    );
  }

  // update course Group
  updatecourseLectures(
    courseLectures: ICourseLectureAddEditVM,
    isDelete: Boolean
  ) {
    this.CourseLectureServ.editCourseLectures(courseLectures).subscribe(
      (res: any) => {
        if (res.returnValue == 1) {
          // get lectures ids from response after edit
          this.WeeklyLecIDSCtrls.setValue(
            res.Course_Lecture.map((lec) => lec.Lecture_id)
          );
          if (isDelete) {
            this.ToastrMsg.success("General.DeleteSuccessfully");
            this.IsAdd = true;
          } else this.ToastrMsg.success("General.UpdateSuccessfully");
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      }
    );
  }

  // delete dialog
  openDeleteDialog(): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.courseId,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deletecourseLectures();
      });
  }

  // delete course Group
  deletecourseLectures() {
    this.WeeklyLecIDSCtrls.setValue([]);
    this.submitted = false;

    let courseLectures: ICourseLectureAddEditVM = {
      subject_id: this.courseId,
      WeeklyLec_IDS: this.WeeklyLecIDSCtrls.value,
    };
    console.log("WeeklyLecIDS", courseLectures);

    this.spinner.show();
    this.updatecourseLectures(courseLectures, true);
  }
}
