import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { IBodyEductionalGroupVM } from "app/dashboard/models/setup/general-setup/educational-group";

@Injectable({
  providedIn: "root",
})
export class EducationalGroupService {
  constructor(private http: HttpClient) {}

  getEducationalGroup(page: number) {
    return this.http.get(
      `${environment.baseUrl}EducationGroup/getEduGroupByPage/${page}`
    );
  }

  getEducationalGroupDropdown() {
    return this.http.get(`${environment.baseUrl}EducationGroup/getAllEduGroup`);
  }

  addupdateEduGroup(data: IBodyEductionalGroupVM) {
    return this.http.post(
      `${environment.baseUrl}EducationGroup/addEditEduGroup`,
      data
    );
  }

  deleteEducationalGroup(EduGroupId: number) {
    return this.http.delete(
      `${environment.baseUrl}EducationGroup/deleteEduGroup/${EduGroupId}`
    );
  }
}
