import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { AcademicYearService } from "app/dashboard/services/setup/educational-setup/academic-year.service";
import {
  IAcademicYear,
  IAcademicYearList,
  IAddEditAcademicYear,
} from "app/dashboard/models/setup/educational-setup/academic-year";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { DatePipe } from "@angular/common";
// import { IEducationYear } from "app/dashboard/models/setup/educational-setup/education-year";
// import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";

@Component({
  selector: "app-academic-year",
  templateUrl: "./academic-year.component.html",
  styleUrls: ["./academic-year.component.scss"],
})
export class AcademicYearComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  curLang: string;
  EduCompId: number;
  // educationalYearList: IEducationYear[];
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any = [
  //   { functionName: "Update" },
  //   { functionName: "Delete" },
  //   { functionName: "Terms" },
  //   { functionName: "Class-rooms" },
  // ];
  displayColumns: string[] = ["#", "fromDate", "toDate", "eduYear", "option"];
  dataSource: MatTableDataSource<IAcademicYear>;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private academicYearService: AcademicYearService,
    // private educationYearService: EducationYearService,
    private route: ActivatedRoute,
    public authService: AuthService,
    private datePipe: DatePipe
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      From_Date: ["", Validators.required],
      To_Date: ["", Validators.required],
      stageId: ["", Validators.required],
      eduYearId: ["", Validators.required],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.returnAllAcademicYear(1);
    // this.getAllEducationalYear();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnAllAcademicYear(1);
      // this.getAllEducationalYear();
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "eduYear":
          return this.curLang == "ar" ? item.eduYearArName : item.eduEnName;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  // getAllEducationalYear() {
  //   this.spinner.show();
  //   this.educationYearService.getEducationalYearDropdown().subscribe((res) => {
  //     if (res) this.educationalYearList = res.eduYearList;
  //     this.spinner.hide();
  //   });
  // }

  editAcademicYear(AcadYearData: IAcademicYear) {
    this.myForm.patchValue(AcadYearData);
    this.FormCtrls.id.setValue(AcadYearData.academicEduYearId);
    this.FormCtrls.From_Date.setValue(AcadYearData.fromDate);
    this.FormCtrls.To_Date.setValue(AcadYearData.toDate);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // Return All  Academic  Year
  returnAllAcademicYear(page: number) {
    this.spinner.show();
    this.academicYearService
      .getAcademicYears(page)
      .subscribe((res: IAcademicYearList) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.eduYearList);
          this.itemsCounts = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (
      new Date(this.FormCtrls.To_Date.value).getTime() <
      new Date(this.FormCtrls.From_Date.value).getTime()
    ) {
      this.toastr.error("General.ToDateBeforeFromDate");
      return;
    }

    let AcademicYearData: IAddEditAcademicYear = {
      id: this.FormCtrls.id.value,
      From_Date: this.datePipe.transform(this.FormCtrls.From_Date.value),
      To_Date: this.datePipe.transform(this.FormCtrls.To_Date.value),
      eduYearId: this.FormCtrls.eduYearId.value,
      eduCompId: this.EduCompId,
    };
    // console.log(AcademicYearData);

    if (this.myForm.valid) {
      this.spinner.show();
      this.academicYearService
        .addEditAcademicYear(AcademicYearData)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            let returnObject = res.academicEducationYear;
            let returnData: IAcademicYear = {
              academicEduYearId: returnObject.id,
              fromDate: returnObject.From_Date,
              toDate: returnObject.To_Date,
              stageId: returnObject.stageId,
              eduYearId: returnObject.eduYearId,
              eduYearArName: returnObject.eduYear_ar_name,
              eduEnName: returnObject.eduYear_en_name,
            };

            AcademicYearData.id == 0
              ? this.addAcademicYear(returnData)
              : this.updateAcademicYear(returnData);
            this.dataSource.sort = this.sort;
            // this.clearForm();
            this.submitted = false;
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add  Academic  Year
  addAcademicYear(res: IAcademicYear) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");

    this.FormCtrls.eduYearId.reset();
    this.submitted = false;
  }

  // Update  Academic  Year
  updateAcademicYear(res: IAcademicYear) {
    let i = this.dataSource.data.findIndex(
      (e) => e.academicEduYearId == res.academicEduYearId
    );
    this.dataSource.data[i] = res;
    this.toastr.success("General.UpdateSuccessfully");

    this.clearForm();
  }

  openDeleateDialog(AcadYearData: IAcademicYear): void {
    let title = `${this.datePipe.transform(
      AcadYearData.fromDate
    )} - ${this.datePipe.transform(AcadYearData.toDate)}`;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: title,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteAcademicYear(AcadYearData);
      });
  }

  // Start:Delete  Academic  Year
  deleteAcademicYear(AcadYearData: IAcademicYear) {
    this.academicYearService
      .deleteAcademicYear(AcadYearData.academicEduYearId)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex((e) => e == AcadYearData);
          this.dataSource.data.splice(index, 1);
          // this.itemsCounts--;
          this.dataSource.sort = this.sort;

          if (AcadYearData.academicEduYearId == this.FormCtrls.id.value)
            this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
