import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { EducationYearService } from "app/dashboard/services/setup/educational-setup/education-year.service";
import {
  IAddEditEducationYear,
  IEducationYear,
  IEducationYearList,
} from "app/dashboard/models/setup/educational-setup/education-year";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-education-year",
  templateUrl: "./education-year.component.html",
  styleUrls: ["./education-year.component.scss"],
})
export class EducationYearComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Update" },
  //   { functionName: "Delete" },
  // ];
  curLang: string;
  displayColumns: string[] = ["#", "ar_name", "en_name", "stage", "option"];
  dataSource: MatTableDataSource<IEducationYear>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    public authService: AuthService,
    private educationYearService: EducationYearService
  ) {
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      eduYear_ar_ame: ["", [Validators.required, CustomeValidator.whiteSpace]],
      eduYear_en_ame: ["", [Validators.required, CustomeValidator.whiteSpace]],
      stageID: ["", Validators.required],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.returnEduYears(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnEduYears(1);
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "stage":
          return this.curLang == "ar" ? item.stageArName : item.stageEnName;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  editEduYear(EduYearData: IEducationYear) {
    // this.myForm.patchValue(EduYearData);
    this.FormCtrls.id.setValue(EduYearData.eduYearId);
    this.FormCtrls.eduYear_ar_ame.setValue(EduYearData.ar_name);
    this.FormCtrls.eduYear_en_ame.setValue(EduYearData.en_name);
    this.FormCtrls.stageID.setValue(EduYearData.stageId);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // Return All Edu Years
  returnEduYears(page: number) {
    this.spinner.show();
    this.educationYearService
      .getAllEducationalYears(page)
      .subscribe((res: IEducationYearList) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.eduYearList);
          this.itemsCounts = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;
    let EduYearData: IAddEditEducationYear = {
      id: this.FormCtrls.id.value,
      stageID: this.FormCtrls.stageID.value,
      eduYear_ar_ame: this.FormCtrls.eduYear_ar_ame.value,
      eduYear_en_ame: this.FormCtrls.eduYear_en_ame.value,
      eduCompId: this.EduCompId,
    };

    if (this.myForm.valid) {
      this.spinner.show();
      this.educationYearService
        .addEditEducationalYear(EduYearData)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            let returnObject = res.new_eduYear;
            let returnData: IEducationYear = {
              eduYearId: returnObject.id,
              ar_name: returnObject.ar_name,
              en_name: returnObject.en_name,
              stageId: returnObject.stageID,
              stageArName: returnObject.stage_ar_name,
              stageEnName: returnObject.group_en_name,
            };

            EduYearData.id == 0
              ? this.addEduYear(returnData)
              : this.updateEduYear(returnData);
            this.dataSource.sort = this.sort;
            this.clearForm();
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add Edu Year
  addEduYear(res: IEducationYear) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");
  }

  // Update Edu Year
  updateEduYear(res: IEducationYear) {
    let index = this.dataSource.data.findIndex(
      (e) => e.eduYearId == res.eduYearId
    );
    this.dataSource.data[index] = res;
    this.toastr.success("General.UpdateSuccessfully");
  }

  openDeleateDialog(EduYearData: IEducationYear): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? EduYearData.ar_name : EduYearData.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteEduYear(EduYearData);
      });
  }

  // Start:Delete Edu Year
  deleteEduYear(EduYearData: IEducationYear) {
    this.educationYearService
      .deleteEducationalYear(EduYearData.eduYearId)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex((e) => e == EduYearData);
          this.dataSource.data.splice(index, 1);
          // this.itemsCounts--;
          this.dataSource.sort = this.sort;

          if (EduYearData.eduYearId == this.FormCtrls.id.value)
            this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
