import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import {
  IAddEditAssignClassRoomVM,
  IAssignClassRoom,
  IAssignClassRoomList,
} from "app/dashboard/models/setup/educational-setup/assign-class-rooms";

@Injectable({
  providedIn: "root",
})
export class AsignClassRoomsService {
  constructor(private http: HttpClient) {}

  // Return Class rooms
  getAsignClassRoomsByYearId(yearId: any, page: number) {
    return this.http.get<IAssignClassRoomList>(
      `${environment.baseUrl}AssignClassRoom/getAssignClassRoomByAcademicEduYearId/${yearId}/${page}`
    );
  }

  // getAsignClassRooms(page: number) {
  //   return this.http.get<IAssignClassRoomList>(
  //     `${environment.baseUrl}AssignClassRoom/getAssignClassRoomPage/${page}`
  //   );
  // }

  // getAsignClassRoomsDropDown(page: number) {
  //   return this.http.get<IAssignClassRoom[]>(
  //     `${environment.baseUrl}AssignClassRoom/getAllAssignClassRoom/${page}`
  //   );
  // }

  //Add New Class room
  addEditAsignClassRoom(data: IAddEditAssignClassRoomVM) {
    return this.http.post(
      `${environment.baseUrl}AssignClassRoom/addEditAssignClassRoom`,
      data
    );
  }

  //Delete Class room
  deleteAsignClassRoom(ClassRoomId: number) {
    return this.http.delete(
      `${environment.baseUrl}AssignClassRoom/deleteAssignClassRoom/${ClassRoomId}`
    );
  }
}
