import { Component, Inject, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";
import {
  ITopicBodyVM,
  ITopicVM,
} from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-topics",
  templateUrl: "./topics.component.html",
  styleUrls: ["./topics.component.scss"],
})
export class TopicsComponent implements OnInit {
  topics: ITopicVM[];
  myForm: FormGroup;
  submitted = false;
  p: number = 1;
  maxSize: number = 9;
  itemsCount: number;
  lessonId: number;
  constructor(
    private spinner: NgxSpinnerService,
    private msg: MessagesService,
    private dialog: MatDialog,
    private fb: FormBuilder,
    private courceStructureService: CourceStructureService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<TopicsComponent>
  ) {
    this.lessonId = data.lessonId;
  }

  ngOnInit(): void {
    this.getTopicsByLessonId();
    this.myForm = this.fb.group({
      id: [0],
      topic_ar_name: [
        "",
        [
          Validators.required,
          Validators.maxLength(100),
          CustomeValidator.whiteSpace,
        ],
      ],
      topic_en_name: [""],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getTopicsByLessonId() {
    this.spinner.show();
    this.courceStructureService
      .getTopicsByLessonId(this.p, this.lessonId)
      .subscribe((res: any) => {
        //  console.log("getTopicsByLessonId", res);
        this.topics = res.edu_topic;
        this.itemsCount = res.topicsSize;
        this.spinner.hide();
      });
  }

  getTopicById(topic: ITopicVM) {
    this.myForm.patchValue(topic);
    // console.log(topic);
  }

  getTopicByIds(id: any) {
    this.spinner.show();
    this.courceStructureService
      .getTopicById(this.lessonId, id)
      .subscribe((res: any) => {
        if (res.id != null) {
          this.myForm.patchValue(res);
          // this.myForm.controls["id"].setValue(res.LessonID);
          // this.myForm.controls["id"].setValue(res.id);
          // this.myForm.controls["topic_ar_name"].setValue(res.topic_ar_name);
          // this.myForm.controls["topic_en_name"].setValue(res.topic_en_name);

          this.spinner.hide();
        }
      });
  }

  addUpdateTopic() {
    this.submitted = true;
    let body = {} as ITopicBodyVM;
    body.LessonID = this.lessonId;
    body.id = this.FormCtrls.id?.value;
    body.topic_ar_name = this.FormCtrls.topic_ar_name.value;
    body.topic_en_name = this.FormCtrls.topic_en_name.value;
    body.state = true;
    if (this.myForm.valid) {
      this.spinner.show();
      this.courceStructureService.addUpdateTopic(body).subscribe((res: any) => {
        // console.log("addUpdateTopic", res);
        if (res.returnValue == 1) {
          this.submitted = false;
          this.myForm.reset();
          this.FormCtrls.id?.setValue(0);
          this.getTopicsByLessonId();
          body.id == 0
            ? this.msg.success("General.AddSuccessfully")
            : this.msg.success("General.UpdeteSuccessfully");
        }
        this.spinner.hide();
      });
    }
  }

  openDeleateDialog(topic: ITopicVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: topic.topic_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteTopic(topic);
      });
  }

  deleteTopic(topic: ITopicVM) {
    this.spinner.show();
    this.courceStructureService.deleteTopic(topic.id).subscribe((res: any) => {
      if (res.returnValue == 1) {
        this.msg.success("General.DeleteSuccessfully");
        let i = this.topics.indexOf(topic);
        this.topics.splice(i, 1);
      }
      this.spinner.hide();
    });
  }

  track(index: number, el: any): number {
    return el.id;
  }
}
