import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { TermsService } from "app/dashboard/services/setup/educational-setup/terms.service";
import { ActivatedRoute } from "@angular/router";
import {
  ITermGetVM,
  ITermList,
  ITermAddEdidVM,
} from "app/dashboard/models/setup/educational-setup/terms";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { AuthService } from "app/shared/services/auth/auth.service";
import { DatePipe } from "@angular/common";

@Component({
  selector: "app-terms",
  templateUrl: "./terms.component.html",
  styleUrls: ["./terms.component.scss"],
})
export class TermsComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  academicEduYearId: number;
  curLang: string;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Update" },
  //   { functionName: "Delete" },
  //   { functionName: "Detail" },
  // ];
  displayColumns: string[] = [
    "#",
    "ar_name",
    "en_name",
    "fromDate",
    "toDate",
    "option",
  ];
  dataSource: MatTableDataSource<ITermGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private termsService: TermsService,
    public authService: AuthService,
    private datePipe: DatePipe
  ) {
    this.route.paramMap.subscribe(
      (param) => (this.academicEduYearId = Number(param.get("yearId")))
    );

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      fromDate: ["", Validators.required],
      toDate: ["", Validators.required],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.returnAllTermsByYearId(1);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnAllTermsByYearId(1);
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  editTerm(TermData: ITermGetVM) {
    this.myForm.patchValue(TermData);
    this.FormCtrls.id.setValue(TermData.TermId);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // Return All  Terms
  returnAllTermsByYearId(page: number) {
    this.spinner.show();
    this.termsService
      .getTermsByYearId(this.academicEduYearId, page)
      .subscribe((res: ITermList) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.TermList);
          this.itemsCounts = res.countItems;
          this.dataSource.sort = this.sort;
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    if (
      new Date(this.FormCtrls.toDate.value).getTime() <
      new Date(this.FormCtrls.fromDate.value).getTime()
    ) {
      this.toastr.error("General.ToDateBeforeFromDate");
      return;
    }

    let TermData: ITermAddEdidVM = {
      id: this.FormCtrls.id.value,
      ar_name: this.FormCtrls.ar_name.value,
      en_name: this.FormCtrls.en_name.value,
      fromDate: this.datePipe.transform(this.FormCtrls.fromDate.value),
      toDate: this.datePipe.transform(this.FormCtrls.toDate.value),
      academicEduYearId: this.academicEduYearId,
      eduCompId: this.EduCompId,
    };
    // console.log(TermData);

    if (this.myForm.valid) {
      this.spinner.show();
      this.termsService.addEditTerm(TermData).subscribe((res: any) => {
        if (res.returnValue == 1) {
          // let returnObject = res.academicTerm;
          // let returnData: ITermGetVM = {
          //   TermId: returnObject.id,
          //   ar_name: returnObject.ar_name,
          //   en_name: returnObject.en_name,
          //   fromDate: returnObject.fromDate,
          //   toDate: returnObject.toDate,
          // };
          res.academicTerm.TermId = res.academicTerm.id;

          TermData.id == 0
            ? this.addTerm(res.academicTerm)
            : this.updateTerm(res.academicTerm);
          this.dataSource.sort = this.sort;
          this.clearForm();
        } else this.toastr.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add Term
  addTerm(res: ITermGetVM) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");
  }

  // Update Term
  updateTerm(res: ITermGetVM) {
    console.log(res);
    let i = this.dataSource.data.findIndex((e) => e.TermId == res.TermId);
    this.dataSource.data[i] = res;
    this.toastr.success("General.AddSuccessfully");
  }

  openDeleateDialog(TermData: ITermGetVM): void {
    if (TermData.TermId == this.FormCtrls.id.value) this.clearForm();

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.curLang == "ar" ? TermData.ar_name : TermData.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteTerm(TermData);
      });
  }

  // Delete Term
  deleteTerm(TermData: ITermGetVM) {
    this.termsService.deleteTerm(TermData.TermId).subscribe((res: any) => {
      if (res.returnValue == 1) {
        let index = this.dataSource.data.findIndex((e) => e == TermData);
        this.dataSource.data.splice(index, 1);
        // this.itemsCounts--;
        this.dataSource.sort = this.sort;

        if (TermData.TermId == this.FormCtrls.id.value) this.clearForm();

        this.toastr.success("General.DeleteSuccessfully");
      } else {
        this.toastr.error("General.TosterErrorMessage");
      }
    });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
