import { Observable } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import {
  StudentGetResponseVM,
  StudentGroupAddEditVM,
  StudentGroupGetResponseVM,
  StudentGroupGetVM,
  StudentResultVM,
  StudentSearchVM,
} from "app/dashboard/models/setup/educational-setup/student-group";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class StudentGroupService {
  constructor(private http: HttpClient) {}

  //#region >> groups
  // Return All  StudentGroups by page
  getAllStudentGroups(page: number) {
    return this.http.get<StudentGroupGetResponseVM>(
      `${environment.baseUrl}StudentGroups/getAllStudentGroups/${page}`
    );
  }

  // Return All StudentGroups > dropDown list
  getAllStudentGroupsDropDown() {
    return this.http.get<StudentGroupGetVM[]>(
      `${environment.baseUrl}StudentGroups/getAllStudentGroupsDropDown`
    );
  }

  // Add New StudentGroup
  addStudentGroup(data: StudentGroupAddEditVM) {
    return this.http.post(
      `${environment.baseUrl}StudentGroups/addStudentGroup`,
      data
    );
  }

  // Update StudentGroup
  updateStudentGroup(id: number, data: StudentGroupAddEditVM) {
    return this.http.put(
      `${environment.baseUrl}StudentGroups/updateStudentGroup${id}`,
      data
    );
  }

  // Delete StudentGroup
  deleteStudentGroup(id: number) {
    return this.http.delete(
      `${environment.baseUrl}StudentGroups/deleteStudentGroup/${id}`
    );
  }
  //#endregion

  //#region assign student to group
  getAllStudentsInGroup(body: StudentSearchVM) {
    return this.http.post<StudentGetResponseVM>(
      `${environment.baseUrl}studyinggroup/search-student-groups`,
      body
    );
  }

  checkStudent(body: StudentSearchVM) {
    return this.http.post<StudentResultVM>(
      environment.baseUrl + "studyinggroup/search-student-in-group",
      body
    );
  }

  addStudent(studentId: number, groupId: number) {
    return this.http.get(
      `${environment.baseUrl}studyinggroup/add-student-groups/${studentId}/${groupId}`
    );
  }

  deleteStudent(studentId: number, groupId: number) {
    return this.http.delete(
      `${environment.baseUrl}studyinggroup/remove-student-groups/${studentId}/${groupId}`
    );
  }

  uploadStudentsByExcel(body: any) {
    return this.http.post(
      environment.baseUrl + "studyinggroup/upload-excel-student-groups",
      body
    );
  }
  //#endregion
}
