import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { IAddEditEduYearDetailes } from "app/dashboard/models/setup/educational-setup/education-detail";
@Injectable({
  providedIn: "root",
})
export class EducationDetailService {
  constructor(private http: HttpClient) {}

  // Return All Academic Educational Year Detail
  getAcademicEduYearDetailsByTermId(TermId: any, page: number) {
    return this.http.get(
      `${environment.baseUrl}AcademicEduYearDetails/getEducationDetailsByTermIdByPage/${TermId}/${page}`
    );
  }

  getAcademicEduYearDetailsByPage(page: number) {
    return this.http.get(
      `${environment.baseUrl}AcademicEduYearDetails/getEducationDetailsByPage/${page}`
    );
  }

  //Add New Academic Educational Year Detail
  AddEditAcademicYearDetail(data: IAddEditEduYearDetailes) {
    return this.http.post(
      `${environment.baseUrl}AcademicEduYearDetails/addEditEducationDetails`,
      data
    );
  }

  //Delete Academic Educational Year Detail
  deleteAcademicEducationalYearDetail(AcademicEduYearDetailId: number) {
    return this.http.delete(
      `${environment.baseUrl}AcademicEduYearDetails/deleteEducationDetails/${AcademicEduYearDetailId}`
    );
  }
}
