import { environment } from "environments/environment";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { ExamNightModel } from "app/dashboard/models/setup/educational-setup/exam-night";

@Injectable({
  providedIn: "root",
})
export class ExamNightService {
  constructor(private http: HttpClient) {}

  getAllExamNight(page: number) {
    return this.http.get(`${environment.baseUrl}AssignClassRoom${page}`);
  }

  addExamNight(body: ExamNightModel) {
    return this.http.post(`${environment.baseUrl}AssignClassRoom`, body);
  }

  deleteFile(id: number) {
    return this.http.delete(`${environment.baseUrl}AssignClassRoom${id}`);
  }
}
