import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  CourseAddEditVM,
  CourseGetVM,
} from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { MessagesService } from "app/shared/services/messages.service";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { switchMap } from "rxjs/operators";
import { environment } from "environments/environment";

@Component({
  selector: "app-add-edit-course",
  templateUrl: "./add-edit-course.component.html",
  styleUrls: ["./add-edit-course.component.scss"],
})
export class AddEditCourseComponent implements OnInit {
  Course: CourseGetVM;
  courseId: number = 0;
  imageChange: boolean = false;
  removeImage: number = 0;

  myForm: FormGroup;
  submitted: boolean = false;
  currentLang: string;

  EduCompId: number;
  functionId: number;

  constructor(
    private fb: FormBuilder,
    private CourseServ: CourseService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private uploadFileService: UploadFileService,
    private route: ActivatedRoute,
    private router: Router,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
    });

    route.paramMap.subscribe((params) => {
      let id = Number(params.get("courseId"));
      if (id) {
        this.courseId = id;
        // console.log(id);
        CourseServ.getCourseById(id).subscribe((res) => {
          this.Course = res[0];
          this.myForm.patchValue(this.Course);
        });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      subject_ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      subject_en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      price: ["", [Validators.required, CustomeValidator.minusNum]],
      subjectCode: ["", [Validators.required, CustomeValidator.whiteSpace]],
      academicEduYearId: ["", [Validators.required]],
      MaxScore: ["", [Validators.required, CustomeValidator.minusNum]],
      MinScore: ["", [Validators.required, CustomeValidator.minusNum]],
      course_attach: [null],
      PromoPath: [""],
      subjectDescription: [""],
    });
  }

  ngAfterViewInit() {
    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
    });
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // select image
  onChangeImage(event: any) {
    this.removeImage = 0;
    this.uploadFileService.uploadFile(event).then((file) => {
      this.formCtrls.course_attach?.setValue(file);
      this.imageChange = true;
    });
  }

  imageUrl() {
    let imageUrl = !this.imageChange
      ? this.Course?.ImagePath
      : this.formCtrls?.course_attach?.value;
    this.uploadFileService.previewPicture(imageUrl);
  }

  onSubmit() {
    this.submitted = true;
    // console.log(this.myForm.value);

    if (this.myForm.valid) {
      if (this.formCtrls.MinScore?.value > this.formCtrls.MaxScore?.value)
        return this.ToastrMsg.error("EducationalSetup.maxScoreGreaterThanMin");

      let course: CourseAddEditVM = {
        eduCompId: this.EduCompId,
        id: this.courseId,
        subject_ar_name: this.formCtrls.subject_ar_name?.value,
        subject_en_name: this.formCtrls.subject_en_name?.value,
        price: this.formCtrls.price?.value,
        subjectCode: this.formCtrls.subjectCode?.value,
        MaxScore: this.formCtrls.MaxScore?.value,
        MinScore: this.formCtrls.MinScore?.value,
        PromoPath: this.formCtrls.PromoPath?.value,
        course_attach: this.formCtrls.course_attach?.value,
        subjectDescription: this.formCtrls.subjectDescription?.value,
        academicEduYearId: this.formCtrls.academicEduYearId?.value,
        remove_image: this.removeImage,
      };
      // if (
      //   this.formCtrls.course_attach.value == null &&
      //   this.imageChange == true
      // )
      //   course.remove_image = 1;

      console.log("Course", course);

      this.CourseServ.addEditCourse(course).subscribe((res: any) => {
        if (res.returnValue == 1) {
          course.id == 0
            ? this.ToastrMsg.success("General.AddSuccessfully")
            : this.ToastrMsg.success("General.UpdateSuccessfully");

          this.router.navigate(["dashboard/educational-setup/courses/index/"], {
            queryParams: {
              functionId: this.functionId,
            },
          });
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  RemoveFile() {
    // this.imageChange = true;
    this.removeImage = 1;
    if (this.Course) this.Course.ImagePath = null;
    this.formCtrls.course_attach.setValue(null);
  }

  // clearForm() {
  //   this.myForm.reset();
  //   this.courseId = 0;
  //   this.submitted = false;
  // }
}
