import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { AcademicYearComponent } from "./academic-year/academic-year/academic-year.component";
import { AsignClassRoomsComponent } from "./academic-year/asign-class-rooms/asign-class-rooms.component";
import { TermDetailComponent } from "./academic-year/term-detail/term-detail.component";
import { TermsComponent } from "./academic-year/terms/terms.component";
import { DepartmentComponent } from "./department/department.component";
import { EducationYearComponent } from "./education-year/education-year.component";
import { SubgroupsComponent } from "./groups/subgroups/subgroups.component";
import { GroupsComponent } from "./groups/groups/groups.component";
import { CourseIndexComponent } from "./courses/course-index/course-index.component";
import { AddEditCourseComponent } from "./courses/add-edit-course/add-edit-course.component";
import { CourseGroupComponent } from "./courses/course-group/course-group.component";
import { StudentGroupIndexComponent } from "./student-groups/student-group-index/student-group-index.component";
import { AddStudentToGroupComponent } from "./student-groups/add-student-to-group/add-student-to-group.component";
import { StrctureIndexComponent } from "./courses/course-strcture/strcture-index/strcture-index.component";
import { CourseLectureComponent } from "./courses/course-lecture/course-lecture.component";
import { ExamNightComponent } from "./courses/exam-night/exam-night.component";

const routes: Routes = [
  { path: "educational-year", component: EducationYearComponent },
  { path: "department", component: DepartmentComponent },
  {
    path: "academic-year",
    children: [
      { path: "", redirectTo: "AcademicYear", pathMatch: "full" },

      {
        path: "AcademicYear",
        component: AcademicYearComponent,
      },
      {
        path: "terms/:yearId",
        component: TermsComponent,
      },
      {
        path: "term-details/:yearId/:termId",
        component: TermDetailComponent,
      },
      {
        path: "assign-class-rooms/:yearId",
        component: AsignClassRoomsComponent,
      },
    ],
  },

  {
    path: "groups",
    component: GroupsComponent,
  },
  {
    path: "subgroups",
    component: SubgroupsComponent,
  },

  {
    path: "courses",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: "full",
      },
      {
        path: "index",
        component: CourseIndexComponent,
      },
      {
        path: "edit-course/:courseId",
        component: AddEditCourseComponent,
      },
      {
        path: "add-course",
        component: AddEditCourseComponent,
      },
      {
        path: "course-lectures/:courseId",
        component: CourseLectureComponent,
      },
      {
        path: "course-group",
        component: CourseGroupComponent,
      },
      {
        path: "course-structure/:subjectId",
        component: StrctureIndexComponent,
      },

      {
        path: "exam-night",
        component: ExamNightComponent,
      },
    ],
  },
  {
    path: "student-group",
    component: StudentGroupIndexComponent,
  },
  {
    path: "add-student-to-group",
    component: AddStudentToGroupComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EducationalSetupRoutingModule {}
