import { CourseGroupGetVM } from "app/dashboard/models/setup/educational-setup/course";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { Component, OnInit, ViewChild } from "@angular/core";
import { NgxSpinnerService } from "ngx-spinner";
import { MessagesService } from "app/shared/services/messages.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatSort } from "@angular/material/sort";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { TranslateService } from "@ngx-translate/core";
import { EducationDetailService } from "app/dashboard/services/setup/educational-setup/education-detail.service";
import {
  IAddEditEduYearDetailes,
  IEduYearDetailes,
  IEduYearDetailesList,
} from "app/dashboard/models/setup/educational-setup/education-detail";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";

@Component({
  selector: "app-term-detail",
  templateUrl: "./term-detail.component.html",
  styleUrls: ["./term-detail.component.scss"],
})
export class TermDetailComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  itemsCounts: number = 0;
  termId: number;
  academicEduYearId: number;
  curLang: string;
  EduCompId: number;
  functionId: number;
  rowFunctions: IRowFunctionVM[];
  // rowFunctions: any[] = [
  //   { functionName: "Update" },
  //   { functionName: "Delete" },
  // ];
  displayColumns: string[] = [
    "#",
    "Course",
    "CourseGroup",
    "CoursePercentage",
    "AddedToScores",
    "option",
  ];
  dataSource: MatTableDataSource<IEduYearDetailes>;
  CourseGroupList: CourseGroupGetVM[];
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: MessagesService,
    private dialog: MatDialog,
    private translate: TranslateService,
    private fb: FormBuilder,
    private educationDetailService: EducationDetailService,
    private CourseService: CourseService,
    private route: ActivatedRoute,
    public authService: AuthService
  ) {
    this.route.paramMap.subscribe((pram) => {
      this.termId = Number(pram.get("termId"));
      this.academicEduYearId = Number(pram.get("yearId"));
    });

    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      if (this.functionId) {
        this.authService
          .getUserRowFunctions(this.functionId)
          .subscribe((res: IRowFunctionVM[]) => {
            if (res.length > 0) {
              this.rowFunctions = res;
            }
          });
      }
    });

    this.EduCompId = Number(authService.getEduCompId());
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [0],
      SubjectId: ["", [Validators.required]],
      CoursesGroupId: ["", [Validators.required]],
      CoursePercentage: [
        "",
        [Validators.required, CustomeValidator.minusNum, Validators.max(99)],
      ],
      AddedToScores: ["", [Validators.required]],
    });

    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });

    this.returnEduYearDetails(1);
    this.getAllCoursesGroup();
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }

    this.authService.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.returnEduYearDetails(1);
      this.getAllCoursesGroup();
    });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "Course":
          return this.curLang == "ar"
            ? item.subject_ar_name ?? item.subject_en_name
            : item.subject_en_name ?? item.subject_ar_name;
        case "CourseGroup":
          return this.curLang == "ar"
            ? item.courseGroup_arName ?? item.courseGroup_enName
            : item.courseGroup_enName ?? item.courseGroup_arName;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  editeduYearDetail(eduYearDetailData: IEduYearDetailes) {
    this.myForm.patchValue(eduYearDetailData);
    this.FormCtrls.id.setValue(eduYearDetailData.edu_details_id);
    this.FormCtrls.SubjectId.setValue(eduYearDetailData.subject_id);
    this.FormCtrls.CoursesGroupId.setValue(eduYearDetailData.courseGroup_id);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  // Return Courses Group
  getAllCoursesGroup() {
    this.CourseService.getAllCourseGroupsDropDown().subscribe((res) => {
      if (res) this.CourseGroupList = res;
    });
  }

  // Return Educational Year Details
  returnEduYearDetails(page: number) {
    this.spinner.show();
    this.educationDetailService
      .getAcademicEduYearDetailsByTermId(this.termId, page)
      .subscribe((res: IEduYearDetailesList) => {
        if (res) {
          this.dataSource = new MatTableDataSource(res.edu_details_list);
          this.itemsCounts = res.countItems;
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  onSubmit() {
    this.submitted = true;

    let eduYearDetailData: IAddEditEduYearDetailes = {
      id: this.FormCtrls.id.value,
      TermId: this.termId,
      SubjectId: this.FormCtrls.SubjectId.value,
      CoursesGroupId: this.FormCtrls.CoursesGroupId.value,
      CoursePercentage: this.FormCtrls.CoursePercentage.value,
      AddedToScores: this.FormCtrls.AddedToScores.value,
      eduCompId: this.EduCompId,
    };
    console.log(eduYearDetailData);

    if (this.myForm.valid) {
      this.spinner.show();
      this.educationDetailService
        .AddEditAcademicYearDetail(eduYearDetailData)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            let returnObject = res.academicEducationYearDetails;
            let returnData: IEduYearDetailes = {
              edu_details_id: returnObject.id,
              subject_id: returnObject.SubjectId,
              subject_ar_name: returnObject.subject_arName,
              subject_en_name: returnObject.subject_enName,
              courseGroup_id: returnObject.CoursesGroupId,
              courseGroup_arName: returnObject.courseGroup_arName,
              courseGroup_enName: returnObject.courseGroup_enName,
              CoursePercentage: returnObject.CoursePercentage,
              AddedToScores: returnObject.AddedToScores,
            };
            console.log(res);

            eduYearDetailData.id == 0
              ? this.addEduYearDetail(returnData)
              : this.updateEduYearDetail(returnData);
            this.dataSource.sort = this.sort;
            this.clearForm();
          } else this.toastr.error(res?.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  // Add Educational Year Detail
  addEduYearDetail(res: IEduYearDetailes) {
    this.dataSource.data.unshift(res);
    this.itemsCounts++;
    this.toastr.success("General.AddSuccessfully");
  }

  // Update Educational Year Detail
  updateEduYearDetail(res: IEduYearDetailes) {
    let i = this.dataSource.data.findIndex(
      (e) => e.edu_details_id == res.edu_details_id
    );
    this.dataSource.data[i] = res;
    this.toastr.success("General.UpdateSuccessfully");
  }

  openDeleateDialog(eduYearDetailData: IEduYearDetailes): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg:
            this.curLang == "ar"
              ? eduYearDetailData.subject_ar_name ??
                eduYearDetailData.subject_en_name
              : eduYearDetailData.subject_en_name ??
                eduYearDetailData.subject_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteeduYearDetail(eduYearDetailData);
      });
  }

  // Start:Delete Educational Year Detail
  deleteeduYearDetail(eduYearDetailData: IEduYearDetailes) {
    this.educationDetailService
      .deleteAcademicEducationalYearDetail(eduYearDetailData.edu_details_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let index = this.dataSource.data.findIndex(
            (e) => e == eduYearDetailData
          );
          this.dataSource.data.splice(index, 1);
          // this.itemsCounts--;
          this.dataSource.sort = this.sort;

          if (eduYearDetailData.edu_details_id == this.FormCtrls.id.value)
            this.clearForm();

          this.toastr.success("General.DeleteSuccessfully");
        } else {
          this.toastr.error("General.TosterErrorMessage");
        }
      });
  }

  clearForm() {
    this.myForm.reset();
    this.FormCtrls.id.setValue(0);
    this.submitted = false;
  }
}
