 import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ActivatedRoute } from "@angular/router";
import { IaddUpdateLessonVM, ILessonBodyVM } from "app/dashboard/models/setup/educational-setup/cource-structure";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";

import { MessagesService } from "app/shared/services/messages.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-add-lesson",
  templateUrl: "./add-lesson.component.html",
  styleUrls: ["./add-lesson.component.scss"],
})
export class AddLessonComponent implements OnInit {
  unitId: number;
  lessonId: number;
  myForm!: FormGroup;
  submitted = false;
  lesson_name: string;
  coverPath = "https://via.placeholder.com/150";
  constructor(
    private fb: FormBuilder,
    private msg: MessagesService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private courceStructureService: CourceStructureService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddLessonComponent>
  ) {
    this.unitId = data.unitId;
    this.lessonId = data.lesson.id;
    if (this.lessonId > 0) this.getLessonById();
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [this.lessonId],
      lesson_ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      lesson_en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      lessonDescription: [""],
      thumbnailPic: [null],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getLessonById() {
    this.spinner.show();
    this.courceStructureService.getLessonById(this.lessonId).subscribe((res: any) => {
      if (res.addedLesson) {
        this.myForm.patchValue(res.addedLesson);
        this.lesson_name = res.addedLesson.lesson_ar_name;
        this.coverPath = `mozakretyapi${res.addedLesson.lessonThumb?.attach_path}`;
        this.spinner.hide();
      }
    });
  }

  addUpdateLesson() {
    this.submitted = true;
    let body = {} as IaddUpdateLessonVM;
    let addedLesson = {} as ILessonBodyVM;

    addedLesson.id = this.FormCtrls.id?.value;
    addedLesson.lesson_ar_name = this.FormCtrls.lesson_ar_name?.value;
    addedLesson.lesson_en_name = this.FormCtrls.lesson_en_name?.value;
    addedLesson.lessonDescription = this.FormCtrls.lessonDescription?.value;
    addedLesson.thumbnailPic = this.FormCtrls.thumbnailPic?.value;
    body.addedLesson = addedLesson;

    if (this.myForm.valid) {
      this.spinner.show();
      this.courceStructureService.addUpdateLesson(this.unitId, body).subscribe(
        (res) => {
          if (res) {
            this.dialogRef.close(res);
            this.spinner.hide();
            this.lessonId == 0
              ? this.msg.success("General.AddSuccessfully")
              : this.msg.success("General.UpdeteSuccessfully");
          }
        }
      );
    }
  }

  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.coverPath = fileReder;
        this.FormCtrls.thumbnailPic.setValue(data);
      };
    }
  }
}
