import { NgxSpinnerService } from "ngx-spinner";
import { ActivatedRoute } from "@angular/router";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MessagesService } from "app/shared/services/messages.service";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";
import { CourceStructureService } from "app/dashboard/services/setup/educational-setup/cource-structure.service";
import {
  currentSubject,
  IAddedUnitVM,
  IAddUpdateUnitVM,
} from "app/dashboard/models/setup/educational-setup/cource-structure";

@Component({
  selector: "app-add-unit",
  templateUrl: "./add-unit.component.html",
  styleUrls: ["./add-unit.component.scss"],
})
export class AddUnitComponent implements OnInit {
  unitId: number;
  subjectId: number;
  myForm!: FormGroup;
  submitted: boolean = false;
   coverPath = "https://via.placeholder.com/150";
  unit_name: string;
  constructor(
    private fb: FormBuilder,
    private msg: MessagesService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private courceStructureService: CourceStructureService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddUnitComponent>
  ) {
    this.subjectId = data.subjectId;
     console.log("subjectId", this.subjectId);
    this.unitId = data.unitId;
    if (this.unitId > 0) this.getUnitById();
  }

  ngOnInit(): void {
    this.myForm = this.fb.group({
      id: [this.unitId],
      unit_ar_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      unit_en_name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      unitDescription: [""],
      thumbnailPic: [null],
    });
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  // get Unit ById
  getUnitById() {
    this.spinner.show();
    this.courceStructureService
      .getUnitById(this.unitId)
      .subscribe((res: any) => {
        if (res.addedUnit) {
          this.myForm.patchValue(res.addedUnit);
          this.unit_name = res.addedUnit.unit_ar_name;
          this.subjectId = res.addedUnit.subjectid;
          this.coverPath = `mozakretyapi${res.addedUnit.unitThumb?.attach_path}`;
          this.spinner.hide();
        }
      });
  }

  // Start: add Unit
  addUpdateUnit() {
    this.submitted = true;
    let body = {} as IAddUpdateUnitVM;
    let addedUnit = {} as IAddedUnitVM;
    let currentSubject = {} as currentSubject;

    addedUnit.id = this.FormCtrls.id?.value;
    addedUnit.unit_ar_name = this.FormCtrls.unit_ar_name?.value;
    addedUnit.unit_en_name = this.FormCtrls.unit_en_name?.value;
    addedUnit.unitDescription = this.FormCtrls.unitDescription?.value;
    addedUnit.thumbnailPic = this.FormCtrls.thumbnailPic?.value;
    currentSubject.subject_id = this.subjectId;
    body.addedUnit = addedUnit;
    body.currentSubject = currentSubject;

    if (this.myForm.valid) {
      this.spinner.show();
      this.courceStructureService.addUpdateUnit(body).subscribe((res) => {
        console.log("addUpdateUnit", res);
        if (res) {
          this.spinner.hide();
          this.dialogRef.close(res);
          this.unitId == 0
            ? this.msg.success("General.AddSuccessfully")
            : this.msg.success("General.UpdeteSuccessfully");
        }
      });
    }
  }

  onChange(event: any) {
    let fileName = <File>event.target.files[0].name;
    let fileSize = <File>event.target.files[0].size;
    let fileType = <File>event.target.files[0].type;
    let LastModified = <File>event.target.files[0].lastModified;
    let LastModifiedDate = <File>event.target.files[0].lastModifiedDate;

    if (event.target.files) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = (event: any) => {
        let fileReder = event.target.result;
        let data = {
          FileAsBase64: fileReder,
          name: fileName,
          size: fileSize,
          type: fileType,
          LastModified: LastModified,
          LastModifiedDate: LastModifiedDate,
        };
        this.coverPath = fileReder;
        this.FormCtrls.thumbnailPic.setValue(data);
      };
    }
  }
}
