import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  SubgroupGetResponseVM,
  SubgroupGetVM,
  SubgroupAddEditVM,
  GroupGetVM,
} from "app/dashboard/models/setup/educational-setup/group";
import { GroupService } from "app/dashboard/services/setup/educational-setup/group.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-subgroups",
  templateUrl: "./subgroups.component.html",
  styleUrls: ["./subgroups.component.scss"],
})
export class SubgroupsComponent implements OnInit, AfterViewInit {
  Subgroups: SubgroupGetVM[] = [
    {
      Id: 1,
      Ar_Name: "",
      En_Name: "ff",
      Group: { Id: 1, Ar_Name: "", En_Name: "df" },
    },
    {
      Id: 2,
      Ar_Name: "",
      En_Name: "ee",
      Group: { Id: 2, Ar_Name: "", En_Name: "aa" },
    },
  ];
  myForm!: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang!: string;
  groups: GroupGetVM[] = [
    { Id: 1, Ar_Name: "", En_Name: "ff" },
    { Id: 2, Ar_Name: "", En_Name: "kk" },
  ];

  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "Ar_Name", "En_Name", "Group", "actions"];
  dataSource: MatTableDataSource<SubgroupGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private GroupServ: GroupService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.getAllSubgroups(1);

    this.myForm = this.fb.group({
      Id: [null],
      Ar_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      En_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      GroupId: ["", [Validators.required]],
    });

    // get groups
    this.getAllGroups();

    // test data
    this.dataSource = new MatTableDataSource(this.Subgroups);
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;

    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "Group":
          return this.currentLang == "ar"
            ? item.Group?.Ar_Name
            : item.Group?.En_Name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;

    // custom filter
    this.dataSource.filterPredicate = (data: SubgroupGetVM, filter) => {
      return this.currentLang == "ar"
        ? data.Group.Ar_Name.toLocaleLowerCase().indexOf(filter) != -1
        : data.Group.En_Name.toLocaleLowerCase().indexOf(filter) != -1;
    };
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get groups >> dropDown
  getAllGroups() {
    this.spinner.show();
    this.GroupServ.getAllGroupsDropDown<GroupGetVM>().subscribe((res) => {
      if (res) this.groups = res;
      this.spinner.hide();
    });
  }

  // get all subgroups
  getAllSubgroups(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.GroupServ.getAllSubgroups<SubgroupGetResponseVM>(page).subscribe(
      (res) => {
        if (res) {
          this.Subgroups = res.SubgroupList;
          this.dataSource = new MatTableDataSource(this.Subgroups);
          this.itemsCounts = res.countItems;
          console.log("Subgroups: >> ", res);
        }
        this.spinner.hide();
      }
    );
  }

  // add subgroup
  addSubgroup() {
    this.submitted = true;

    if (this.myForm.valid) {
      let subgroup = this.myForm.value;
      delete subgroup.Id;
      console.log("Subgroup", subgroup);

      this.spinner.show();
      this.GroupServ.addSubgroup<SubgroupAddEditVM>(subgroup).subscribe(
        (res: any) => {
          if (res?.returnValue == 1) {
            this.getAllSubgroups(this.curPage);
            this.ToastrMsg.success("General.AddSuccessfully");

            this.myForm.reset();
            this.submitted = false;
          } else {
            this.ToastrMsg.error(res?.returnString);
          }
          this.spinner.hide();
        }
      );
    }
  }

  // get form values to update
  toEditSubgroup(subgroup: SubgroupGetVM) {
    this.myForm.patchValue(subgroup);
    this.formCtrls.GroupId?.setValue(subgroup.Group.Id);
  }

  // update subgroup
  updateSubgroup() {
    this.submitted = true;

    if (this.myForm.valid) {
      let id = this.formCtrls.Id.value;
      let subgroup = this.myForm.value;
      delete subgroup.Id;
      console.log("Subgroup", subgroup);

      this.spinner.show();
      this.GroupServ.updateSubgroup<SubgroupAddEditVM>(id, subgroup).subscribe(
        (res: any) => {
          if (res?.returnValue == 1) {
            this.getAllSubgroups(this.curPage);
            this.ToastrMsg.success("General.UpdateSuccessfully");

            this.myForm.reset();
            this.submitted = false;
          } else {
            this.ToastrMsg.error(res?.returnString);
          }
          this.spinner.hide();
        }
      );
    }
  }

  // delete dialog
  openDeleteDialog(subgroup: SubgroupGetVM): void {
    let subgroupName =
      this.currentLang == "ar" ? subgroup.Ar_Name : subgroup.En_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: subgroupName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteSubgroup(subgroup.Id);
      });
  }

  // delete subgroup
  deleteSubgroup(id: any) {
    let index = this.Subgroups.indexOf(id);

    this.spinner.show();
    this.GroupServ.deleteSubgroup(id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.Subgroups.splice(index, 1);
        this.dataSource = new MatTableDataSource(this.Subgroups);
        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
