import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  StudentGroupAddEditVM,
  StudentGroupGetVM,
} from "app/dashboard/models/setup/educational-setup/student-group";
import { StudentGroupService } from "app/dashboard/services/setup/educational-setup/student-group.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { CourseService } from "app/dashboard/services/setup/educational-setup/course.service";
import { CourseGetVM } from "app/dashboard/models/setup/educational-setup/course";

@Component({
  selector: "app-student-group-index",
  templateUrl: "./student-group-index.component.html",
  styleUrls: ["./student-group-index.component.scss"],
})
export class StudentGroupIndexComponent implements OnInit {
  StudentGroups: StudentGroupGetVM[] = [];
  teachers: any[];
  courses: CourseGetVM[];

  myForm: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang: string;

  EduCompId: number;
  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [
    { functionName: "Edit" },
    { functionName: "Delete" },
    { functionName: "AddStudentToGroup" },
  ];

  // table
  displayedColumns: string[] = [
    "#",
    "Name",
    "Teacher",
    "Course",
    "MaxLimit",
    "actions",
  ];
  dataSource: MatTableDataSource<StudentGroupGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private studentGroupServ: StudentGroupService,
    private courseServ: CourseService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });

    this.EduCompId = Number(authserv.getEduCompId());
  }

  ngOnInit(): void {
    this.getAllStudentGroups(1);
    this.getAllTeachers();
    this.getAllCourses();

    this.myForm = this.fb.group({
      Id: [0],
      Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      TeacherId: ["", [Validators.required]],
      CourseId: ["", [Validators.required]],
      MaxLimit: ["", [Validators.required, CustomeValidator.minusNum]],
    });

    // test data
    this.dataSource = new MatTableDataSource(this.StudentGroups);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }

    this.authserv.EduCompId.subscribe((e) => {
      this.EduCompId = e.EduCompId;
      this.getAllStudentGroups(1);
      this.getAllTeachers();
      this.getAllCourses();
    });
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  getAllTeachers() {}

  getAllCourses() {
    this.spinner.show();
    this.courseServ.getAllCoursesDropDown().subscribe((res: any) => {
      if (res) this.courses = res;
      this.spinner.hide();
    });
  }

  // get all course Groups
  getAllStudentGroups(page: number) {
    this.curPage = page;

    this.spinner.show();
    this.studentGroupServ.getAllStudentGroups(page).subscribe((res) => {
      if (res) {
        this.StudentGroups = res.StudentGroupList;
        this.dataSource = new MatTableDataSource(this.StudentGroups);
        this.itemsCounts = res.countItems;
        console.log("StudentGroups: >> ", res);
      }
      this.spinner.hide();
    });
  }

  onSubmit() {
    this.submitted = true;
    if (this.myForm.valid) {
      let studentGroup = {} as StudentGroupAddEditVM;
      studentGroup.EduCompId = this.EduCompId;
      studentGroup.Name = this.formCtrls.Name?.value;
      studentGroup.TeacherId = this.formCtrls.TeacherId?.value;
      studentGroup.CourseId = this.formCtrls.CourseId?.value;
      studentGroup.MaxLimit = this.formCtrls.MaxLimit?.value;
      console.log("StudentGroup", studentGroup);

      this.formCtrls.Id?.value
        ? this.updateStudentGroup(studentGroup)
        : this.addStudentGroup(studentGroup);
    } else this.ToastrMsg.error("General.FormNotValid");
  }

  // add course Group
  addStudentGroup(studentGroup: StudentGroupAddEditVM) {
    this.spinner.show();
    this.studentGroupServ
      .addStudentGroup(studentGroup)
      .subscribe((res: any) => {
        if (res?.returnValue == 1) {
          this.getAllStudentGroups(this.curPage);
          this.ToastrMsg.success("General.AddSuccessfully");

          this.myForm.reset();
          this.formCtrls.id.setValue(0);
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  // get form values to update
  toEditStudentGroup(studentGroup: StudentGroupGetVM) {
    this.myForm.patchValue(studentGroup);
    this.formCtrls.TeacherId?.setValue(studentGroup.Teacher.Id);
    this.formCtrls.CourseId?.setValue(studentGroup.Course.id);
  }

  // update course Group
  updateStudentGroup(studentGroup: StudentGroupAddEditVM) {
    this.spinner.show();
    this.studentGroupServ
      .updateStudentGroup(this.formCtrls.Id.value, studentGroup)
      .subscribe((res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex(
            (e) => e.Id == this.formCtrls.Id.value
          );
          this.dataSource.data[i] = res;

          this.ToastrMsg.success("General.UpdateSuccessfully");
          this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      });
  }

  // delete dialog
  openDeleteDialog(studentGroup: StudentGroupGetVM): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: studentGroup.Name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteStudentGroup(studentGroup);
      });
  }

  // delete course Group
  deleteStudentGroup(studentGroup: StudentGroupGetVM) {
    this.spinner.show();
    this.studentGroupServ
      .deleteStudentGroup(studentGroup.Id)
      .subscribe((res: any) => {
        if (res?.returnValue == 1) {
          let i = this.dataSource.data.findIndex((e) => e == studentGroup);
          this.dataSource.data.splice(i, 1);

          this.ToastrMsg.success("General.DeleteSuccessfully");
        } else this.ToastrMsg.error(res?.returnString);
        this.spinner.hide();
      });
  }

  clearForm() {
    this.myForm.reset();
    this.formCtrls.id.setValue(0);
    this.submitted = false;
  }
}
