import { AfterViewInit, Component, OnInit, ViewChild } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { MatDialog } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import {
  GroupAddEditVM,
  GroupGetResponseVM,
  GroupGetVM,
} from "app/dashboard/models/setup/educational-setup/group";
import { GroupService } from "app/dashboard/services/setup/educational-setup/group.service";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { IRowFunctionVM } from "app/shared/models/general/general";
import { NgxSpinnerService } from "ngx-spinner";
import { AuthService } from "app/shared/services/auth/auth.service";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";

@Component({
  selector: "app-groups",
  templateUrl: "./groups.component.html",
  styleUrls: ["./groups.component.scss"],
})
export class GroupsComponent implements OnInit, AfterViewInit {
  Groups: GroupGetVM[] = [
    { Id: 1, Ar_Name: "", En_Name: "ff" },
    { Id: 2, Ar_Name: "", En_Name: "kk" },
  ];
  myForm!: FormGroup;
  submitted = false;
  curPage: number = 1;
  itemsCounts: number;
  currentLang!: string;

  functionId: number;
  // rowFunctions: IRowFunctionVM[];
  rowFunctions: any[] = [{ functionName: "Edit" }, { functionName: "Delete" }];

  // table
  displayedColumns: string[] = ["#", "Ar_Name", "En_Name", "actions"];
  dataSource: MatTableDataSource<GroupGetVM>;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private GroupServ: GroupService,
    private translate: TranslateService,
    private spinner: NgxSpinnerService,
    private ToastrMsg: MessagesService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    public authserv: AuthService
  ) {
    //use after auth for function id and row functions
    route.queryParamMap.subscribe((params) => {
      this.functionId = Number(params.get("functionId"));
      // console.log("functionId", this.functionId);
      if (this.functionId) {
        this.authserv
          .getUserRowFunctions(this.functionId)
          .subscribe((res: any) => {
            if (res.length > 0) {
              // console.log("rowFunctions", res);
              this.rowFunctions = res;
            }
          });
      }
    });

    // current language
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
  }

  ngOnInit(): void {
    this.getAllGroups(1);

    this.myForm = this.fb.group({
      Id: [null],
      Ar_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
      En_Name: ["", [Validators.required, CustomeValidator.whiteSpace]],
    });

    // test data
    this.dataSource = new MatTableDataSource(this.Groups);
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  get formCtrls() {
    return this.myForm.controls;
  }

  // get all groups
  getAllGroups(page: number) {
    this.curPage = page;
    this.spinner.show();

    this.GroupServ.getAllGroups<GroupGetResponseVM>(
      page
    ).subscribe((res) => {
      if (res) {
        this.Groups = res.GroupList;
        this.dataSource = new MatTableDataSource(this.Groups);
        this.itemsCounts = res.countItems;
        console.log("Groups: >> ", res);
      }
      this.spinner.hide();
    });
  }

  // add group
  addGroup() {
    this.submitted = true;

    if (this.myForm.valid) {
      let group = this.myForm.value;
      delete group.Id;
      console.log("Group", group);

      this.spinner.show();
      this.GroupServ.addGroup<GroupAddEditVM>(
        group
      ).subscribe((res: any) => {
        if (res?.returnValue == 1) {
          this.getAllGroups(this.curPage);
          this.ToastrMsg.success("General.AddSuccessfully");

          this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      });
    }
  }

  // get form values to update
  toEditGroup(group: GroupGetVM) {
    this.myForm.patchValue(group);
  }

  // update group
  updateGroup() {
    this.submitted = true;

    if (this.myForm.valid) {
      let id = this.formCtrls.Id.value;
      let group = this.myForm.value;
      delete group.Id;
      console.log("Group", group);

      this.spinner.show();
      this.GroupServ.updateGroup<GroupAddEditVM>(
        id,
        group
      ).subscribe((res: any) => {
        if (res?.returnValue == 1) {
          this.getAllGroups(this.curPage);
          this.ToastrMsg.success("General.UpdateSuccessfully");

          this.myForm.reset();
          this.submitted = false;
        } else {
          this.ToastrMsg.error(res?.returnString);
        }
        this.spinner.hide();
      });
    }
  }

  // delete dialog
  openDeleteDialog(group: GroupGetVM): void {
    let groupName =
      this.currentLang == "ar" ? group.Ar_Name : group.En_Name;

    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: groupName,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteGroup(group.Id);
      });
  }

  // delete group
  deleteGroup(id: any) {
    let index = this.Groups.indexOf(id);

    this.spinner.show();
    this.GroupServ.deleteGroup(id).subscribe((res: any) => {
      if (res?.returnValue == 1) {
        this.Groups.splice(index, 1);
        this.dataSource = new MatTableDataSource(this.Groups);
        this.ToastrMsg.info("General.DeleteSuccessfully");
      } else {
        this.ToastrMsg.error(res?.returnString);
      }
      this.spinner.hide();
    });
  }
}
